import { useSelector } from "react-redux";
import { State } from "../../redux/types";
import { calcYearsEnding } from "../../utils/lingvo";
import {
  optionsActivity,
  optionsPurpose,
  optionsSex,
} from "../../utils/optionsLists";
import { MenuProps } from "../../utils/types";
import {
  Calories,
  EditProfileButton,
  MenuContainer,
  Name,
  Parameter,
  ProfileData,
  Purpose,
} from "./Menu.styles";

function Menu({ onClose }: MenuProps) {
  const data = useSelector((state: State) => state.profile);

  const bmi = Math.round(data.weight / ((data.stature / 100) ^ 2));

  let bmiText = "";

  if (bmi <= 16) {
    bmiText = "–≤—ã—Ä–∞–∂–µ–Ω–Ω—ã–π –¥–µ—Ñ–∏—Ü–∏—Ç";
  } else if (bmi > 16 && bmi <= 18.5) {
    bmiText = "–¥–µ—Ñ–∏—Ü–∏—Ç";
  } else if (bmi > 18.5 && bmi <= 25) {
    bmiText = "–Ω–æ—Ä–º–∞";
  } else if (bmi > 25 && bmi <= 30) {
    bmiText = "–∏–∑–±—ã—Ç–æ—á–Ω–∞—è –º–∞—Å—Å–∞";
  } else if (bmi > 30 && bmi <= 35) {
    bmiText = "–æ–∂–∏—Ä–µ–Ω–∏–µ 1 —Å—Ç–µ–ø–µ–Ω–∏";
  } else if (bmi > 35 && bmi <= 40) {
    bmiText = "–æ–∂–∏—Ä–µ–Ω–∏–µ 2 —Å—Ç–µ–ø–µ–Ω–∏";
  } else if (bmi > 40) {
    bmiText = "–æ–∂–∏—Ä–µ–Ω–∏–µ 3 —Å—Ç–µ–ø–µ–Ω–∏";
  }

  return (
    <MenuContainer>
      <ProfileData>
        <Name>{data.name}</Name>
        <Parameter>
          {!data.sex ? "" : optionsSex.find((o) => o.value === data.sex)!.text}
        </Parameter>
        <Parameter>
          {data.age} {calcYearsEnding(data.age)}, {data.stature} —Å–º,{" "}
          {data.weight} –∫–≥
        </Parameter>
        <Parameter>
          {!data.activityLevel
            ? ""
            : optionsActivity.find((o) => o.value === data.activityLevel)!
                .span}{" "}
          —É—Ä–æ–≤–µ–Ω—å –∞–∫—Ç–∏–≤–Ω–æ—Å—Ç–∏
        </Parameter>

        <Parameter>
          –ò–ú–¢ {bmi} ({bmiText})
        </Parameter>

        <Purpose>
          <Parameter>
            –¶–µ–ª—å:{" "}
            {!data.purpose
              ? ""
              : optionsPurpose.find((o) => o.value === data.purpose)!.text}
          </Parameter>
        </Purpose>
      </ProfileData>
      <Calories>{data.purposeMetabolism} –∫–∫–∞–ª</Calories>
      <EditProfileButton to="/profile" onClick={onClose}>
        –†–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞—Ç—å –ª–∏—á–Ω—ã–µ –¥–∞–Ω–Ω—ã–µ
      </EditProfileButton>
    </MenuContainer>
  );
}

export default Menu;
